import axios from "axios";
import React,{useState} from "react";
import Masonry from "react-masonry-component";
import Image from "./Image";
import "./index.css";

const Search=()=>{
  const [input, setinput] = useState([])
  const [image,setimage] =useState([])
  // console.log(input);
  const getPhoto=(e)=>{
e.preventDefault();
console.log('submiteing the form')
axios.get(`https://api.unsplash.com/search/photos?per_page=30&query=${input}&client_id=my7-InaCg43c3NVpbnRy8EK3S7YnEEdr1_hKumpzWZc`)
.then((data) => {
  setimage(data.data.results)

})
 }
return(
    <>
    <div className="container mt-3">
      <h4 className="text-dark text-center" className="h1">search your image</h4>
      <form className="d-flex">
        <input className="form-control me-2" type="search" value={input} onChange={(event)=>{setinput(event.target.value)}}  placeholder="type here to search image" aria-label="Search"/>
        <button className="btn btn-warning" onClick={getPhoto} type="submit">Search</button>
        </form>
    </div>

    <div className=" mx-4 my-3">
      <Masonry options={{isFitWidth:true}}>
      {image.map(value => 
          <Image src={value.urls.small} key={value.id}  alt="pic" ></Image>
          )
        }
        </Masonry>
      </div>
    </>
)
}
export default Search;